import React from "react";

import { Grid, Tooltip, Typography } from "@material-ui/core";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { FaCircle } from "react-icons/fa";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        circle: {
            paddingLeft: theme.spacing(1),
        },
        description: {
            "& span": {
                fontWeight: 500,
            },
            textAlign: "right",
            width: 140,
        },
        emptyCircle: {
            fill: "lightgray",
        },
        score: {
            maxWidth: 130,
        },
        solidCircle: {
            fill: "#0377BD",
        },
    })
);

interface Role {
    description: string;
    score: number;
}

interface Props {
    roles: Array<Role>;
}

const range: any = {
    1: "< 2 years",
    2: "2-4 years",
    3: "4-8 years",
    4: "8-10 years",
    5: "10+ years",
};

export default function(props: Props) {
    const classes = useStyles(props);
    const { roles } = props;

    return (
        <Grid container direction="column" alignItems="flex-end" spacing={1}>
            {roles.map((item: Role, idx: number) => (
                <Grid item key={idx}>
                    <Grid container spacing={2}>
                        <Grid item className={classes.description}>
                            <Typography variant="caption">{item.description}</Typography>
                        </Grid>
                        <Grid item className={classes.score}>
                            {Array.from(Array(item.score), (_, idx) => (
                                <Tooltip key={idx} title={idx === item.score - 1 ? range[idx + 1] : ""}>
                                    <span className={classes.circle}>
                                        <FaCircle className={classes.solidCircle} />
                                    </span>
                                </Tooltip>
                            ))}
                            {Array.from(Array(5 - item.score), (_, idx) => (
                                <span key={idx} className={classes.circle}>
                                    <FaCircle className={classes.emptyCircle} />
                                </span>
                            ))}
                        </Grid>
                    </Grid>
                </Grid>
            ))}
        </Grid>
    );
}
